.mainslider {
	.slide {
		&__img {
			display: block;
			width: 100%;
			height: auto; } }
	.slider-counter {
		display: flex;
		align-items: flex-end;
		justify-content: center;
		margin: 0 15px;
		&__all {
			font-size: 14px; }
		&__curr {
			font-size: 18px;
			font-weight: 700; } }
	.slick-arrow {
		width: 30px;
		height: 30px;
		border-radius: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		color: $white;
		@include trs;
		background: $orange;
		z-index: 2;
		cursor: pointer;
		&:before {
			font-size: 20px; }
		&:hover {
			background: $dark; }
		&.left {
			transform: translate(0, 0);
			&:before {
				transform: rotate(90deg); } }
		&.right {
			transform: translate(0, 0);
			&:before {
				transform: rotate(-90deg); } } }
	.bottom {
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: 30px; } }
