.nice-select {
	padding: 0;
	margin: 0;
	border: none;
	background: none;
	line-height: 1;
	width: auto;
	height: auto;
	min-height: 0;
	border-radius: 0;
	* {
		border-radius: 0;
		min-height: 0;
		padding: 0;
		margin: 0;
		border: none;
		background: none;
		line-height: 1;
		width: auto;
		height: auto; }
	.list {
		border-radius: 0;
		min-height: 0;
		padding: 0;
		margin: 0;
		border: none;
		background: none;
		line-height: 1;
		width: auto;
		height: auto;
		li {
			border-radius: 0;
			line-height: 1;
			padding: 0;
			margin: 0;
			border: none;
			background: none;
			width: auto;
			height: auto;
			min-height: 0; } } }

.category {
	.inner {
		display: flex;
		flex-wrap: wrap; }
	.output {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		flex-wrap: wrap;
		margin-bottom: 35px;
		&-filter {
			width: 220px;
			height: 24px;
			vertical-align: middle;
			border: 1px solid $gray2;
			display: flex;
			align-items: center;
			padding: 0 20px;
			position: relative;
			background: $white;
			@include mq($max-width: 575px) {
				width: 100%;
				margin: 1% 0; }
			&:not(:last-child) {
				margin-right: 30px;
				@include mq($max-width: 575px) {
					margin-right: 0; } }
			&:after {
				border-color: $orange; }
			.current {
				color: $dark;
				text-overflow: ellipsis;
				width: calc(100% - 5px);
				overflow: hidden;
				display: block;
				@include mq($max-width: 575px) {
					font-size: 12px; } }
			.list {
				border: 1px solid $gray2;
				width: 100%;
				top: 120%;
				background: $white;
				li {
					background: $white;
					width: 100%;
					line-height: 2;
					padding: 0 10px;
					@include mq($max-width: 575px) {
						font-size: 12px; }
					&:hover {
						background: $main-bg; } } } } }
	.item {
		width: 31.3%;
		margin: 1%;
		background: $white;
		box-shadow: -11.594px 12.433px 56.42px 5.58px rgba(0, 0, 0, 0.09);
		padding: 30px;
		text-align: center;
		color: $dark;
		@include mq($max-width: 1000px) {
			padding: 15px; }
		@include mq($max-width: 700px) {
			width: 48%; }
		@include mq($max-width: 575px) {
			padding: 10px; }
		@include mq($max-width: 370px) {
			padding: 10px 5px; }
		&.green {
			.status {
				color: $green; } }
		&.red {
			.status {
				color: $red; } }
		&.add {
			.basket-link {
				display: flex; }
			.item__btn {
				background: $green; } }
		&__btn-remove {
			color: $red;
			display: block;
			width: 100%;
			text-align: center;
			margin-top: 10px;
			background: none;
			font-weight: 500;
			font-size: 12px;
			cursor: pointer;
			@include mq($max-width: 575px) {
				font-size: 10px; }
			&:hover {
				color: $dark; } }
		.codes {
			text-align: right;
			padding: 0;
			font-size: 12px;
			width: 100%;
			margin-bottom: 15px;
			@include mq($max-width: 370px) {
				display: none; }
			&-item {
				width: 100%;
				@include mq($max-width: 575px) {
					font-size: 10px; } } }
		.img-wrap {
			margin-bottom: 20px;
			@include mq($max-width: 1000px) {
				margin-bottom: 20px; } }
		&__img {
			display: block;
			max-height: 105px;
			max-width: 90%;
			@include trs;
			margin: 0 auto;
			&:hover {
				transform: scale(.9); } }
		&__title {
			font-size: 16px;
			text-transform: uppercase;
			font-weight: 600;
			min-height: 55px;
			margin-bottom: 10px;
			color: $dark;
			@include mq($max-width: 1000px) {
				margin-bottom: 10px;
				font-size: 16px; }
			@include mq($max-width: 575px) {
				font-size: 12px;
				min-height: 30px; }
			&:hover {
				color: $orange; } }
		.status {
			margin-bottom: 18px;
			@include mq($max-width: 700px) {
				margin-bottom: 10px; } }
		.price {
			width: 100%;
			padding: 18px 0;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			border-bottom: 1px solid  rgb(225, 225, 225);
			border-top: 1px solid  rgb(225, 225, 225);
			@include mq($max-width: 700px) {
				padding: 10px; }
			&__num {
				font-weight: 700;
				font-size: 24px;
				@include mq($max-width: 575px) {
					font-size: 18px; }
				@include mq($max-width: 400px) {
					font-size: 16px; }
				&:after {
					content: '\f158';
					font-family: fontello;
					font-size: 19px;
					margin-left: 5px;
					display: inline-block;
					@include mq($max-width: 575px) {
						font-size: 15px; } } }
			&__old {
				font-size: 16px;
				text-decoration: line-through;
				@include mq($max-width: 575px) {
					font-size: 14px; }
				&:after {
					content: '\f158';
					font-family: fontello;
					font-size: 14px;
					display: inline-block;
					margin-left: 5px;
					@include mq($max-width: 575px) {
						font-size: 12px; } } } }
		.counter-wrap {
			display: flex;
			align-items: center;
			justify-content: center; }
		.basket-link {
			margin-left: 10px;
			width: 35px;
			height: 35px;
			border-radius: 100%;
			background: $orange3;
			color: $white;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			display: none;
			@include mq($max-width: 575px) {
				width: 25px;
				height: 25px;
				font-size: 10px; }
			&:hover {
				background: $dark; }
			.num {
				background: $white;
				border-radius: 5px;
				padding: 2px 0;
				background: $white;
				position: absolute;
				border: 1px solid $gray2;
				z-index: 3;
				right: -30px;
				top: -5px;
				font-size: 12px;
				font-weight: 700;
				color: $dark;
				width: 40px;
				text-align: center;
				@include mq($max-width: 575px) {
					font-size: 8px;
					width: 35px;
					right: -10px;
					top: -10px; } } }
		.counter {
			margin: 18px 0;
			display: flex;
			align-items: center;
			justify-content: center;
			&__num {
				background: none;
				border: none;
				font-size: 14px;
				width: 100px;
				text-align: center;
				width: 40px;
				margin: 0 10px;
				@include mq($max-width: 575px) {
					width: 30px;
					margin: 0 5px; } }
			&__btn {
				width: 25px;
				height: 25px;
				font-size: 16px;
				line-height: 1;
				color: $white;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 100%;
				@include trs;
				background: $orange3;
				cursor: pointer;
				user-select: none;
				&:hover {
					background-image: linear-gradient( 59deg, rgb(182,24,24) 0%, rgb(203,90,8) 100%); } } }
		&__btn {
			display: block;
			width: 100%;
			cursor: pointer; } } }




