html {
	background: $main-bg; }

body {
	font-family: 'Futura';
	font-size: 14px;
	font-weight: 400;
	max-width: 1920px;
	color: $dark;
	margin: 0 auto;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	position: relative;
	&.index-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right, url($img+'bg3-3.jpg') no-repeat bottom 200px left, url($img+'bg4-4-4-4.jpg') no-repeat bottom 200px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right, url($img+'bg3-3.jpg') no-repeat bottom left, url($img+'bg4-4-4-4.jpg') no-repeat bottom right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.catalog-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right, url($img+'bg3-3.jpg') no-repeat bottom 300px left, url($img+'bg4-4-4-4.jpg') no-repeat bottom 200px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right, url($img+'bg3-3.jpg') no-repeat bottom left, url($img+'bg4-4-4-4.jpg') no-repeat bottom right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.reviews-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right, url($img+'bg3.jpg') no-repeat bottom 300px left, url($img+'bg4.jpg') no-repeat bottom 300px right;
		@include mq($max-width: 1700px) {
			background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.category-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right, url($img+'bg3-3.jpg') no-repeat bottom 300px left, url($img+'bg4-4-4.jpg') no-repeat bottom 300px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right, url($img+'bg3-3.jpg') no-repeat bottom left, url($img+'bg4-4-4.jpg') no-repeat bottom right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.contact-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.product-page {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	&.content-page, {
		background: url($img+'bg1.jpg') no-repeat top 180px left, url($img+'bg2.jpg') no-repeat top 180px right, url($img+'bg3-3-3.jpg') no-repeat bottom 200px left, url($img+'bg4-4-4.jpg') no-repeat bottom 250px right;
		@include mq($max-width: 1500px) {
			background: url($img+'bg1.jpg') no-repeat top left, url($img+'bg2.jpg') no-repeat top right, url($img+'bg3-3-3.jpg') no-repeat bottom left, url($img+'bg4-4-4.jpg') no-repeat bottom right; }
		@include mq($max-width: 1200px) {
			background: none; } }
	@include mq($max-width: 1200px) {
		padding: 0; } }

body,
.change-scroll {
	&::-webkit-scrollbar {
		width: 10px; }
	&::-webkit-scrollbar-track {
		background: $gray1; }
	&::-webkit-scrollbar-thumb {
		background: $orange; } }

.sidebar {
	margin-left: auto;
	width: 270px; }

.main {
	margin-right: auto;
	background: $main-bg;
	width: 915px;
	padding: 30px;
	@include mq($max-width: 1200px) {
		width: 100%;
		padding: 0 15px; }
	@include mq($max-width: 575px) {
		padding: 0 10px; }
	&.type2 {
		padding-top: 0;
		padding-right: 0;
		@include mq($max-width: 1200px) {
			padding: 0 15px; }
		@include mq($max-width: 575px) {
			padding: 0 10px; } } }

.container {
	width: 1200px;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		width: 100%;
		padding: 0 15px; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.btn-black {
	width: 270px;
	height: 45px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: $dark;
	border: 1px solid $dark;
	font-size: 16px;
	border-radius: 35px;
	@include trs;
	cursor: pointer;
	@include mq($max-width: 1500px) {
		height: 35px; }
	@include mq($max-width: 1000px) {
		width: auto;
		padding: 0 25px; }
	@include mq($max-width: 575px) {
		font-size: 13px;
		padding: 0 15px;
		height: 30px; }
	&:before {
		display: inline-block;
		margin-right: 10px; }
	&:hover {
		background: $dark;
		color: $white; } }

.btn-orange {
	font-size: 16px;
	border-radius: 35px;
	width: 270px;
	height: 45px;
	display: flex;
	@include trs;
	align-items: center;
	cursor: pointer;
	justify-content: center;
	color: $white;
	background-image: linear-gradient( 59deg, rgb(182,24,24) 0%, rgb(203,90,8) 100%);
	@include mq($max-width: 1500px) {
		height: 35px; }
	@include mq($max-width: 1000px) {
		width: auto;
		padding: 0 25px; }
	@include mq($max-width: 575px) {
		font-size: 13px;
		padding: 0 15px;
		height: 30px; }
	&:before {
		display: inline-block;
		margin-right: 10px; }
	&:hover {
		background-image: linear-gradient( 59deg, rgb(182,24,24) 0%, rgb(182,24,24) 100%); } }

.btn-orange2 {
	font-size: 16px;
	border-radius: 35px;
	width: 270px;
	height: 45px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: $dark;
	border: 1px solid $orange;
	cursor: pointer;
	@include trs;
	@include mq($max-width: 1500px) {
		height: 35px; }
	@include mq($max-width: 1000px) {
		height: auto;
		padding: 0 25px; }
	@include mq($max-width: 575px) {
		font-size: 13px;
		padding: 0 15px;
		height: 30px; }
	&:before {
		display: inline-block;
		margin-right: 10px; }
	&:hover {
		background: $orange;
		color: $white; } }

.page-title {
	font-family: 'Montserrat Alternates', sans-serif;
	font-weight: 900;
	font-size: 35px;
	text-align: center;
	margin-bottom: 30px;
	@include mq($max-width: 1000px) {
		font-size: 26px;
		margin-bottom: 15px; }
	@include mq($max-width: 575px) {
		font-size: 20px; } }

.form {
	width: 100%;
	&-item {
		width: 100%;
		margin-bottom: 10px; }
	&-action {
		width: 90%;
		margin: 30px auto 0 auto;
		@include mq($max-width: 575px) {
			margin-top: 10px;
			width: 100%; } }
	&__input {
		min-width: 100%;
		max-width: 100%;
		display: block;
		width: 100%;
		background: $white;
		padding: 15px 40px;
		border-radius: 22px;
		border: 1px solid rgb(209, 209, 209);
		font-size: 16px;
		color: $dark;
		@include trs;
		@include mq($max-width: 575px) {
			height: auto;
			font-size: 12px;
			padding: 10px 15px; }
		&::placeholder {
			font-size: 16px;
			color: $dark;
			@include mq($max-width: 575px) {
				font-size: 12px; } }
		&:hover,
		&:focus {
			border: 1px solid $dark;
			padding-left: 20px;
			@include mq($max-width: 575px) {
				padding-left: 15px; } } }
	textarea {
		min-height: 75px; }
	&__btn {
		width: 100%; } }

.pagination {
	padding: 30px 0;
	ul {
		display: flex;
		align-items: center;
		justify-content: center; }
	li {
		margin: 0 4px; }
	a {
		border-radius: 100%;
		color: $dark;
		font-weight: 500;
		width: 25px;
		height: 25px;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
		@include mq($max-width: 575px) {
			width: 20px;
			height: 20px; }
		&.active,
		&:hover {
			color: $white;
			background: $orange3; }
		&.prev,
		&.next {
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			background: none;
			&:hover {
				&:before {
					color: $orange3; } }
			&:before {
				font-size: 25px;
				color: $gray2;
				@include mq($max-width: 575px) {
					font-size: 16px; } } }
		&.prev {
			margin-right: 10px;
			@include mq($max-width: 575px) {
				margin-right: 0; }
			&:before {
				transform: rotate(90deg); } }
		&.next {
			margin-left: 10px;
			@include mq($max-width: 575px) {
				margin-left: 0; }
			&:before {
				transform: rotate(-90deg); } } } }



